import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
	  {
		  path: '/',
		  redirect: '/Login',
		  component: resolve => require(['../components/page/Home.vue'],resolve),
		  children: [
			  {
				  path: '/homepage',
				  component: resolve => require(['../components/Homepage.vue'],resolve),
                  meta: {
                      admin: true,
                  }
			  },
			  {
				  path: '/instrument/:id',
				  component: resolve => require(['../components/Instrument.vue'],resolve),
                  meta: {
                      admin: true,
                  }
			  },
			  {
				  path: '/drug/:id',
				  component: resolve => require(['../components/Drug.vue'],resolve),
                  meta: {
                      admin: true,
                  }
			  },
			  {
				  path: '/operation',
				  component: resolve => require(['../components/StorageOperation.vue'],resolve),
                  meta: {
                      admin: true,
                  }
			  },
			  {
				  path: '/received',
				  component: resolve => require(['../components/Received.vue'],resolve),
                  meta: {
                      admin: true,
                  }
			  },
			  {
				  path: '/return',
				  component: resolve => require(['../components/Return.vue'],resolve),
                  meta: {
                      admin: true,
                  }
			  },
			  {
				  path: '/records',
				  component: resolve => require(['../components/RecordsManagement.vue'],resolve),
                  meta: {
                      admin: true,
                  }
			  },
			  // {
				//   path: '/addlist',
				//   component: resolve => require(['../components/addList.vue'],resolve),
			  // },
			  {
				  path: '/manifest',
				  component: resolve => require(['../components/AddManifest.vue'],resolve),
                  meta: {
				      a: true,
                  }
			  },
		  ]
	  },
	  {
		  path: '/Login',
		  component: resolve => require(['../components/Login.vue'],resolve),
	  },
      {
          path: '/*',
          redirect: '/Login'
      }
  ]
})



