import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
	state: {
        sideIndex: 1,       //侧边栏高亮
		showSideBar: true,  //侧边栏是否缩进
		// operationPhotoPath: 'http://drug_control.test/storage/',//入库数据图片本地路径
		operationPhotoPath: 'http://drug-control.jootu.tech/storage/',//入库数据图片测试路径
        protocol: '',
        host: '',
	},
	getters: {
		side: state => {
			return state.showSideBar
		}
	},
	mutations: {
		increment (state,n) {
			state.showSideBar = n;
		}
	}
});

export default store;




