// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios';
import Vuex from 'vuex';
import store from './vuex/store';
import 'babel-polyfill';

Vue.config.productionTip = false;

Vue.use(ElementUI);
Vue.use(Vuex);
Vue.prototype.$axios = axios;


router.beforeEach((to,from,next) => {
	//这里判断用户是否登录
	if( to.path == '/Login'){//去登陆页
		next();
	} else{//登陆成功的
		store.state.host = window.location.host;
		store.state.protocol = window.location.protocol+'//';
		if (!localStorage.getItem("user")) {//用户没登陆过
            ElementUI.Message({
                type: 'warning',
                message: '登录信息已过期，请重新登录!',
                duration: '3000',
            });
			next({
				path: '/Login',
			})
		} else {//用户已登陆
            if (to.meta.admin && JSON.parse(localStorage.getItem("user")).is_admin === 0) {
                next();
            } else if (to.meta.a && JSON.parse(localStorage.getItem("user")).is_admin === 1) {
                next();
            } else {
                ElementUI.Message({
                    type: 'warning',
                    message: '没有权限访问,请先登录!',
                    duration: '3000',
                });
                next({
                    path: '/Login',
                })
            }
		}
	}
});


// http request 拦截器
axios.interceptors.request.use(
	config => {
		if (localStorage.getItem("user")) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
			config.headers.Authorization = `Bearer ${JSON.parse(localStorage.getItem("user")).token}`;
		}
		return config;
	},
	err => {
		return Promise.reject(err);
	});

// http response 拦截器
axios.interceptors.response.use(
	response => {
		if(response.data.status == 401){
			localStorage.removeItem('user');
            ElementUI.Message({
                type: 'warning',
                message: '登录信息已过期，请重新登录!',
                duration: '3000',
            });
			router.replace({
				path: '/login',
				query: {redirect: router.currentRoute.fullPath}
			})
		}
		return response;
	},
	error => {
		if (error.response) {
			switch (error.response.status) {
				case 401:
					localStorage.removeItem('user');
                    ElementUI.Message({
                        type: 'warning',
                        message: '登录信息已过期，请重新登录!',
                        duration: '3000',
                    });
					router.replace({
						path: '/login',
						query: {redirect: router.currentRoute.fullPath}
					})
			}
		}
		return Promise.reject(error.response.data)   // 返回接口返回的错误信息
	});



axios.defaults.baseURL = 'api/';

/* eslint-disable no-new */
import './assets/icon2/iconfont.css';
new Vue({
  	el: '#app',
	router,
	store,
  	components: { App },
  	template: '<App/>'
})



